import React, { PureComponent, Fragment } from "react";
import { HELPER, API } from '../utils'
import { CardLoader } from '../placeholder'
import Disqus from 'disqus-react';
import { Header, Navigation } from '../organisms'
interface IPost {
	match: object
}

interface IDisqusConfig {
	url: string,
	identifier: string,
	title: string,
}
interface IDisqus {
	shortname: string,
	config: IDisqusConfig
}

interface IState {
	postData: [],
	isLoading: boolean,
	disqus: IDisqus,
}

class Post extends PureComponent<IPost, IState> {
	
	constructor(props: any) {
		super(props)
		this.state = {
			postData:[],
			isLoading: true,
			disqus: {
				shortname: '',
				config: {
					url: '',
					identifier: '',
					title: '',
				}
			}
		}
	}
	_isMounted = false;

	setLoading = () => {
		this.setState({
			isLoading: true,
		})
	}

	getRouter = () => {
		const { params }: any = this.props.match
		return params
	}

	async componentDidMount() {
		this._isMounted = true;
		const { params }: any = this.props.match
		// console.log('GET PROPRS: ', this.props)

		const listApi = API.BASE_URL
		let endpoint: number;
		const queryString = `&slug=${params.slug}`;

		for(endpoint = 0; endpoint < listApi.length; endpoint++) {
			// console.log(listApi[endpoint])
			const result = await API.getPostQuery(endpoint, queryString)
			if(result.data.length > 0) {

				const { id, author, title, slug } = result.data[0]
				// Set Config For Disqus
				const disqus: IDisqus = {
					shortname: 'bicara',
					config: {
						url: window.location.href,
						identifier: `${id}-${author}-${slug}`,
						title: title.rendered,
					}
				}
				// console.log('MATCH INDEX FOUND	: ', endpoint)
				// console.log('GET RESULTA: ', result.data, 'DISQUS: ', disqus)
				if(this._isMounted){
					// Set State After Everything Mounted
					this.setState({
						postData: result.data,
						isLoading: false,
						disqus
					})
				}
				break;
			}
		}
	}
	componentWillUnmount() {
    this._isMounted = false;
  }
	render() {
		const { postData, disqus, isLoading }: any = this.state

		if(isLoading){
			return <PostLoader/>
		} else {
			const {title, slug, _embedded, content, date}: any = postData[0]
			return (
				<div className="container post">
					<div className="head-post">
						<Header />
						<Navigation />
					</div>
					<div className="row">
						<div className="col-lg-8 col-md-12 col-centered">
							<article className="content-post">
								<div className="content-head">
									<h1 className="title-post" title="abcd-fgh">{title.rendered}</h1>
									<ul className="breadcrumb">
										<li><a href="#">Home</a></li>
										<li><a href="#">{_embedded.author[0].name}</a></li>
										<li><a href="#">{title.rendered}</a></li>
									</ul>
									<div className="publish-date">
										<b>{_embedded.author[0].name}</b> - <em>{HELPER.timeAgo(date)}</em>
									</div>
								</div>
								<div className="main-content" dangerouslySetInnerHTML={{__html: content.rendered}} />
								<br></br>
								<Disqus.CommentCount {...disqus}>
										Komentar
								</Disqus.CommentCount>
								<Disqus.DiscussionEmbed {...disqus} />
							</article>
						</div>
					</div>
				</div>
			);
		}
		
	}
}


const PostLoader = () => {
	return (
		<div className="container post">
			<div className="head-post">
				<Header />
				<Navigation />
			</div>
			<div className="row">
				<div className="col-lg-8 col-md-12 col-centered">
					<article className="content-post">
						<CardLoader/>
					</article>
				</div>
			</div>
		</div>
	);
};


export default Post;