import { format } from 'date-fns';
import id from 'date-fns/locale/id';
import nativeToast from 'native-toast';

const tagRemover = (str: string) => {
	return str.replace(/<[^>]+>/g, "");
};

const decodeEntity = (str: string) => {
	const parse = str.replace(/&#(\d+);/g, function (match, dec) {
		return String.fromCharCode(dec);
	});
	return parse;
};

const encodeEntity = (str: string) => {
	var buf = [];
	for (var i = str.length - 1; i >= 0; i--) {
		buf.unshift(["&#", str[i].charCodeAt(i), ";"].join(""));
	}
	return buf.join("");
};

const wordReplacer = (str: string) => {
	const wordList = [
		{
			word: "SEFSED",
			replace: "Bicara"
		},
		{
			word: "Winpoin",
			replace: "Bicara"
		}
	];
	wordList.forEach(item => {
		str = str.replace(new RegExp(item.word, "ig"), item.replace);
	})
	return str;
};

const wordCount = (str: string) => {
	const stringMatch = str.match(/\b[-?(\w+)?]+\b/gi) || [];
	return stringMatch.length
};

const wordLimit = (str :String, limit :Number) => {
	const words = str.match(/\b[-?(\w+)?]+\b/gi) || [];
	const wordLenght = words.length
	let sentence = [];
	let lastWord = "";
	if (wordLenght >= limit) {
		for (let i = 0; i < limit; i++) {
			sentence.push(words[i]);
		}
		lastWord = "...";
	} else {
		sentence = words;
	}
	return sentence.join(" ") + lastWord;
};

const stripHtml = (html :any) => {
	const tmp = document.createElement("div");
	tmp.innerHTML = html;
	return tmp.textContent || tmp.innerText || "";
};

const getRandomInt = (min: number, max: number) => {
	min = Math.ceil(min);
	max = Math.floor(max);
	return Math.floor(Math.random() * (max - min + 1)) + min;
};

const parsePostTitle = (str: string) => {
	// Step 1 - Replace Everything in to string
	const parseToString = stripHtml(str)
	// Step 2 - Replace Everything in to string
	const replaceWord = wordReplacer(parseToString)
	// Step 3 - Replace Everything in to string
	const limitTitle = wordLimit(replaceWord, 6)

	return limitTitle

}

const parseExcerpt = (str: string) => {
	// Step 1 - Replace Everything in to string
	const parseToString = stripHtml(str)
	// Step 2 - Replace Everything in to string
	const replaceWord = wordReplacer(parseToString)
	// Step 3 - Replace Everything in to string
	const limitTitle = wordLimit(replaceWord, 10)

	return limitTitle
}


const formatDateID = (date: string) => {
	return format(new Date(date), "dddd, DD MMMM YY", { locale: id })
}

const timeAgo = (previous: string) => {

	const msPerMinute = 60 * 1000;
	const msPerHour = msPerMinute * 60;
	const msPerDay = msPerHour * 24;
	// const msPerMonth = msPerDay * 30;
	// const msPerYear = msPerDay * 365;
	const current = new Date()
	const previousDate = new Date(previous)
	const elapsed = +current - +previousDate;
	const maximumDay = 3;

	if (elapsed < msPerMinute) {
		return Math.round(elapsed / 1000) + ' detik lalu';
	}

	else if (elapsed < msPerHour) {
		return Math.round(elapsed / msPerMinute) + ' menit lalu';
	}

	else if (elapsed < msPerDay) {
		return Math.round(elapsed / msPerHour) + ' jam lalu';
	}

	else if (elapsed < maximumDay) {
		return Math.round(elapsed / msPerDay) + ' hari lalu';
	}

	else {
		return formatDateID(previous)
	}

	// else if (elapsed < msPerMonth) {
	//     return Math.round(elapsed/msPerDay) + ' hari lalu';   
	// }

	// else if (elapsed < msPerYear) {
	//     return Math.round(elapsed/msPerMonth) + ' bulan lalu';   
	// }

	// else {
	//     return Math.round(elapsed/msPerYear ) + ' tahun ago';   
	// }
}

const Toast = (message: string, position: string = 'south', timeout: number = 3000) => {
	nativeToast({
		// See Other Option https://github.com/egoist/native-toast
		message,
		position,
		timeout,
	})
}

