import React, { PureComponent, Fragment } from 'react'
import { Link, Route } from 'react-router-dom'
import classNames from 'classnames'

class Header extends PureComponent {

	state = {
		isOpenDropdown: false,
		isOpenSeachBox: false,
	}

	handleDropdownIcon = (isDelay = false) => {
		if(isDelay){
			setTimeout(() => {
				this.setState({
					isOpenDropdown: !this.state.isOpenDropdown
				})
			}, 500)
		} else {
			this.setState({
				isOpenDropdown: !this.state.isOpenDropdown
			})
		}
	}

	handleSeacrhBox = (isDelay = false) => {
		if(isDelay){
			setTimeout(() => {
				this.setState({
					isOpenSeachBox: !this.state.isOpenSeachBox
				})
			}, 500)
		} else {
			this.setState({
				isOpenSeachBox: !this.state.isOpenSeachBox
			})
		}
	}

	render() {
		// Distrcuturing Object
		const { isOpenDropdown, isOpenSeachBox } = this.state
		return (
			<Fragment>
			<header className="header">
				<Route render={({ history }: any) => (
					<div className="logo" onClick={() => { history.push('/') }}>
						<h1>Berita Informasi Dalam Cerita</h1>
					</div>
				)} />
				<div className="wrapper-feature">
					<div className="search" onClick={() => this.handleSeacrhBox()}>
						<i className="fas fa-search ripple"></i>
					</div>
					<div className="dropdown-icon" onClick={() => this.handleDropdownIcon()}>
						<i className="fas fa-bars ripple"></i>
					</div>
				</div>
				<div onClick={() => this.handleDropdownIcon(true)} className={classNames('dropdown-items', {'show' : isOpenDropdown})}>
					<Link to="/" className="item ripple">Login</Link>
					<Link to="/" className="item ripple">Mulai Baru</Link>
				</div>
				<div onClick={() => this.handleDropdownIcon()} className={classNames('dropdown-close', {'show' : isOpenDropdown})}></div>
			</header>

			<div className={classNames('search-box', {'show' : isOpenSeachBox})}>
				<input type="text" placeholder='Apa yang kamu cari? ketik disini..'/>
			</div>
			</Fragment>
		)
	}
}

export default Header;