import {
	Home,
	Post,
	Author,
	Topics,
	Topic
} from '../pages';

const Router = [
	{
		id: 1,
		path: "/",
		component: Home,
		exact: true
	},
	{
		id: 2,
		path: "/topics",
		component: Topics,
		exact: true
	},
	{
		id: 3,
		path: "/topic/:slug",
		component: Topic,
		exact: true
	},
	{
		id: 4,
		path: "/:username?",
		component: Author,
		exact: true
	},
	{
		id: 5,
		path: "/:username?/:slug",
		component: Post,
		exact: true
	},
];

export default Router;