import React, { PureComponent } from 'react';
import ReactDOM from 'react-dom';

interface IProps {
  src: string,
}

class Iframe extends PureComponent<IProps> {

  constructor(props: IProps) {
    super(props)
    this.state = {

    }
  }

  onLoadFrame = () => {
    console.log('OI LOADED')
  }

  componentDidMount () {
    let iframe = ReactDOM.findDOMNode(this.refs.iframe)
    if(iframe){
      iframe.addEventListener('load', this.onLoadFrame);
    } else {
      console.error('Frame Not Found')
    }
    
  }

  render () {
    const iframeStyle: any = {
      width: '100%',
      height: '100%',
      border: '0',
      position: 'absolute',
    }

    return (
      <iframe
        ref="iframe"
        {...this.props}
        frameBorder={'0'}
        width={'100%'}
        height={'100%'}
        style={iframeStyle}
      />
    )
  }

}

export default Iframe