import React, { PureComponent, Fragment } from "react";
import { API, HELPER } from '../utils'
import { Card } from "../molecules"
import { Badges } from "../atom"
import { CardLoader } from "../placeholder"

interface CardData {
	cardId?: string,
	cardType: 'audio' | 'video' | 'standard',
	endpoint: number,
	query?: string,
}
interface ICards {
	handleModal: any
	data: CardData
}
interface IState {
	posts: Array<object>,
	isLoading: boolean,
	postCompose: object,
	isFailure: boolean,
}

class Cards extends PureComponent<ICards, IState> {
	constructor(props: any) {
		super(props)
		this.state = {
			posts: [],
			isLoading: true,
			isFailure: false,
			postCompose: {}
		}
	}

	componentDidMount() {

		const { data } = this.props
		// console.log('GET TYPE: ', data)

		switch(data.cardType){
			case 'standard':
				this.handleStandardCard(data)
				break;
			case 'video':
				this.handleVideoCard(data)
				break;
			case 'audio':
				this.handleAudioCard(data)
				break;
			default:
				this.handleStandardCard(data)
		}
	}
	
	handleStandardCard = (data: CardData): void => {
		API.getPostQuery(data.endpoint, '&per_page=5').then(res => {
			this.postCompose(res)
		}).catch((error: any) => {
			this.setFailure(error)
		})
	}

	handleVideoCard = (data: CardData): void => {
		API.getPostsByFormat(data.endpoint, data.cardType).then(res => {
			this.postCompose(res)
		})
	}

	handleAudioCard = (data: CardData): void => {
		API.getPostsByFormat(data.endpoint, data.cardType).then(res => {
			this.postCompose(res)
		})
	}

	setLoading = (): void => {
		this.setState({
			isLoading: true
		})
	}

	setFailure = (where: any): void => {
		console.log('You Get Errror On: ', where)
		this.setState({
			isFailure: true
		})
	}

	postCompose = (res: any): void => {
		const postList = res.data
		const firsPost = postList.shift()

		this.setState({
			posts: res.data,
			isLoading: false,
			postCompose: {
				postList,
				firsPost
			}
		})
 
	}

	render() {
		const { handleModal, data }: any = this.props
		const { isLoading, isFailure, postCompose }: any = this.state
		const { firsPost, postList}: any = postCompose
		if(isLoading && isFailure) {
			return 'Failure Load content!';
		} else if(!isLoading && !isFailure) {
			return (
				<Fragment>
					<div className="row row-no-padding">

						<div className="col-sm-12 col-md-4 col-lg-4">

							<div className="content-box gradient-1">
								<Card isSmall={false} data={{
									title: HELPER.parsePostTitle(firsPost.title.rendered),
									description: HELPER.parseExcerpt(firsPost.excerpt.rendered),
									image: firsPost._embedded['wp:featuredmedia'][0].source_url,
									dateTime: HELPER.timeAgo(firsPost.date),
									author: firsPost._embedded.author[0].name,
									authorImage: firsPost._embedded.author[0].avatar_urls["48"],
									authorSlug: firsPost._embedded.author[0].slug,
									slug: firsPost.slug
								}}/>
							</div> { /* END CONTENT BOX */}

						</div>

						<div className="col-sm-12 col-md-8 col-lg-8">
							<div className="content-box gradient-2">
								<div className="box-nav">
									<div className="box-label">
										<Badges idCategories={firsPost.categories} endpoint={data.endpoint} />

									</div>
									<div className="button-menu" onClick={() => handleModal('2', 'open')}>
										<div className="ripple">
											<i className="fas fa-ellipsis-v"></i>
										</div>
									</div>
								</div>

								<div className="row row-no-padding cards match-heigh">
									{postList.map((item: any, index: number) =>
										<div className="col-sm-12 col-md-6 col-lg-6 cards-sliding" key={index}>
											<Card isSmall={true} data={{
												title: HELPER.parsePostTitle(item.title.rendered),
												description: HELPER.parseExcerpt(item.excerpt.rendered),
												image: item._embedded['wp:featuredmedia'][0].source_url,
												author: item._embedded.author[0].name,
												dateTime: HELPER.timeAgo(item.date),
												authorImage: item._embedded.author[0].avatar_urls["48"],
												authorSlug: firsPost._embedded.author[0].slug,
												slug: item.slug
											}}/>
										</div>
									)}
								</div>

							</div>

						</div>
					</div>
				</Fragment>
			);
		} else {
			return <ContentLoader />
		}
	}
}

class ContentLoader extends PureComponent {
	render() {
		return (
			<Fragment>
				<div className="row row-no-padding">
					<div className="col-sm-12 col-md-4 col-lg-4">
						<div className="content-box gradient-1">
							<CardLoader/>
						</div>
					</div>
					<div className="col-sm-12 col-md-8 col-lg-8">
						<div className="content-box gradient-2">
							<div className="row row-no-padding cards">
								{[1, 2, 3, 4].map((item, index) =>
									<div className="col-sm-12 col-md-6 col-lg-6 cards-sliding" key={index}>
										<CardLoader />
									</div>
								)}
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		)
	}
}

export default Cards;