import React from "react";
import classNames from "classnames";
import { Link, Route } from 'react-router-dom';

interface CardData {
	title?: string,
	description?: string,
	image?: string,
	author?: string,
	dateTime?: string,
  authorImage?: string,
  slug: string,
  authorSlug: string,
}
interface ICard {
	isSmall: boolean,
	data?: CardData
}
 
const Card: React.SFC<ICard> = (props: any) => {
	const { isSmall, data }: any = props
	const { title, description, image, author, dateTime, authorImage, slug, authorSlug }: CardData = data
  const composeSlug = `${authorSlug}/${slug}`

  return (
    <div className={classNames('card', { small: isSmall } )}>
      <div className="card-image">
        <Link to={composeSlug} className="default-link">
          <img src={image} alt={title} />
        </Link>
      </div>
      
      <div className="card-text ripple">
        <Link to={composeSlug} className="default-link">
          <h2 className="title-text">{title}</h2>
        </Link>
          <p>{description}</p>
      </div>
      <div className="card-footer">
        <div className="left-foot">
          <div className="author-time">
            <img src={authorImage} alt={author} />
            <div className="text">
              <Link to={authorSlug}>{author}</Link> - {dateTime}
          </div>
          </div>
        </div>
        <div className="right-foot ripple">
          <i className="fas fa-share-alt"></i>
        </div>
      </div>
    </div>
  );
}
Card.defaultProps = {
  data: {
    title: 'Default Title Will Define Here..',
    description: 'Default description will appear if you not enter props description..',
    image: '/assets/images/demo/1.png',
    author: 'Admin',
    authorImage:'/favicon.ico',
    dateTime: '10 hours ago.',
    slug: '/',
    authorSlug: '/'
  }
	
};
export default Card;