import React, { PureComponent, Component } from 'react';
import { API, HELPER } from '../utils';
import {FeedLoader} from '../placeholder';
import { Link } from 'react-router-dom';

// MAKE SIMPLE HELPER
const H = HELPER;
interface IState {
		error: boolean,
		hasMore: boolean,
		isLoading: boolean,
		feedList: [],
		pagination: object,
}

interface IProps {

}
export default class FeedList extends PureComponent<IProps, IState> {

	constructor(props: any){
		super(props)

		this.state = {
			error: false,
			hasMore: false,
			isLoading: true,
			feedList: [],
			pagination: {
				page: 1,
				per_page: 10
			}
		}

		// Loader More
		window.onscroll = () => {
			const {
				loadPost,
				state: {
					isLoading,
				},
			} = this;

			// Bails early if:
			// * there's an error
			if (isLoading) return;
			//
			const body = document.body, html = document.documentElement;
			const actualHeight = Math.max( body.scrollHeight, body.offsetHeight,html.clientHeight, html.scrollHeight, html.offsetHeight );
			const scrollHeight = window.innerHeight + document.documentElement.scrollTop;
			// console.log('OFFISITE HRIGHT: ', scrollHeight, 'HEIGHT: ', actualHeight)
			if (actualHeight === scrollHeight) {
				// Reach The Bottom
				console.log('LOAD MORE')
				loadPost(true);
			}
		};
	}

	_isMounted = false;

	componentDidMount() {
		this._isMounted = true;
		this.loadPost()
	}

	componentWillUnmount() {
		window.onscroll = null
		this._isMounted = false;
	}	

	setLoading = () => {
		this.setState({
			isLoading: true,
		})
	}

	setHasMore = () => {
		this.setState({
			hasMore: true,
		})
	}

	setError = () => {
		H.Toast('Opps, Lost Connection!')
		this.setState({
			error: true,
			isLoading: false
		})
	}

	loadPost = (hasMore: boolean = false) => {
		// console.log('IS MORE: ', hasMore)
		// Active Loading
		this.setLoading()
		
		const {page, per_page}: any = this.state.pagination
		const paginationTemp:any = {...this.state.pagination}
		paginationTemp.page = page + 1

		if(hasMore) {
			// Active HasMore
			this.setHasMore()
			// Make Query
			const queryString = `&page=${page}&per_page=${per_page}`;
			// Copy Temporary
			const feedTemp = [...this.state.feedList]
			
			API.getPostQuery(2,queryString).then(res => {
				// console.log('FEEDLIST: ', res)
				const concateList: any = [...feedTemp,...res.data]
				if(this._isMounted) {
					this.setState({
						isLoading: false,
						hasMore: false,
						error: false,
						pagination: paginationTemp,
						feedList: concateList
					})
				}
			}).catch((error: string) => {
				this.setError()
			})
		} else {
			//First Load
			API.getPostList(2,5).then(res => {
				// console.log('FEEDLIST: ', res)
				if(this._isMounted) {
					this.setState({
						isLoading: false,
						error: false,
						pagination: paginationTemp,
						feedList: res.data
					})
				}
			}).catch((error: string) => {
				this.setError()
			})
		}
		// QUERY MUST INPUT
		// https://www.sefsed.com/wp-json/wp/v2/posts?page=2&per_page=5
		
	}


	render() {
		const { feedList, isLoading, hasMore, error }: any = this.state

		if(isLoading && !hasMore && !error) {
			return <FeedContentLoader/>
		}
		return (
			<div className="feeds">
				{
					feedList.map((item: any, index: number) => (
						<div className="feed-item" key={`${item.id}-${index}`}>
							<div className="feed-content	">
								<CategoriesLoader categories={item.categories}/>
								{/* <span className="label">ASTRONOMI BERITA NEWS</span> */}
									<Link to={`${item._embedded.author[0].slug}/${item.slug}`} className="default-link ripple">
										<h2>{H.parsePostTitle(item.title.rendered)}</h2>
									</Link>
									<p dangerouslySetInnerHTML={{__html: H.parseExcerpt(item.excerpt.rendered)}}></p>
								<div className="feed-bottom">
									<div className="column">
										<a>{H.wordReplacer(item._embedded.author[0].name)}</a>
										<span>{H.timeAgo(item.date)}</span>
									</div>
									<div className="column">
									<div className="ripple"><i className="fas fa-ellipsis-h"></i></div>
									</div>
								</div>
							</div>
							<div className="feed-image">
								<div className="thumbnail-cropped" 
										style={{backgroundImage: `url(${item._embedded['wp:featuredmedia'][0].source_url})`} as React.CSSProperties}>
									<img src={item._embedded['wp:featuredmedia'][0].source_url} alt={H.parsePostTitle(item.title.rendered)} />
								</div>
							</div>
						</div>
					))
				}
				{hasMore ? (<FeedContentLoader/>) : ('')}
			</div>
		)
	}
}

class FeedContentLoader extends PureComponent {
	render() {
		return (
			<div className="feeds">
				{
					[1,2,3].map((item: any, index: number) => (
						<div className="feed-item" key={index}>
							<FeedLoader/>
						</div>
					))
				}
			</div>
		);
	}
}

interface ICategoryProps {
	categories: Array<number>
}
class CategoriesLoader extends PureComponent<ICategoryProps> {

	constructor(props: any) {
		super(props)
		this.state = {
			catList: [],
			isLoading: true,
		}
	}

	componentDidMount(){
		const getListId = this.props.categories
		const promiseCat = []
		const endpoint = 2;
		if(getListId.length < 2){
			promiseCat.push(API.getCategory(endpoint, getListId[0]));
		} else {
			promiseCat.push(API.getCategory(endpoint, getListId[0]));
			promiseCat.push(API.getCategory(endpoint, getListId[1]));
		}
		Promise.all(promiseCat).then(res => {
			// console.log('GET ALL PROMISES: ', res)
			const newData = res.map((item: any) => item.data)
			this.setState({
				catList: newData,
				isLoading: false
			})
		})
	}

	render() {
		const { catList, isLoading }: any = this.state

		if(isLoading) {
			return '...'
		}
		return (
			<span className="label">
			{
				catList.map((item: any, index: number) => {
						return (
							<Link className="label-item default-link" to={`/topic/${item.slug}`} key={`${item.id}-${index}`}>
								{ item.name }
							</Link>
						)
				})
			}
			</span>
		);
	}
}