import Request from './Request'


const API_URL: string[] = [
	'http://api.bicara.co.id/xs12s/api'
]
// FOR WP ENDPOINT
const BASE_URL: string[] = [
	'http://api.bicara.co.id/xs12s/wp-json/wp/v2',
	'http://api.bicara.co.id/bx12s/wp-json/wp/v2',
	'https://www.hipwee.com/wp-json/wp/v2',
	'http://api.bicara.co.id/eb12s/wp-json/wp/v2',
	
]

const getPostList = (baseNumber: number, perpage: number = 10) => {
	const config = {
		url: `${BASE_URL[baseNumber]}/posts?_embed=true&per_page=${perpage}`,
		method: 'get'
	}
	return Request(config);
}

const getCategoryList = (baseNumber: number) => {
	const config = {
		url: BASE_URL[baseNumber]+'/categories',
		method: 'get'
	}
	return Request(config)
}

const getCategory = (baseNumber: number, id: number) => {
	const config = {
		url: BASE_URL[baseNumber]+'/categories/'+id,
		method: 'get'
	}
	return Request(config)
}

const getPostBySlug = (baseNumber: number, slug: string) => {
	const queryString = `${BASE_URL[baseNumber]}/posts?_embed=true&slug=${slug}`
	const config = {
		url: queryString,
		method: 'get'
	}
	return Request(config);
}

const getPostQuery = (baseNumber: number, query: string) => {
	const queryString = `${BASE_URL[baseNumber]}/posts?_embed=true${query}`
	const config = {
		url: queryString,
		method: 'get'
	}
	return Request(config);
}

const getPostsByFormat = (baseNumber: number, format: string) => {
	// const queryString = `${BASE_URL[baseNumber]}/posts?_embed=true&format=${format}`
	const queryString = `${BASE_URL[baseNumber]}/posts?_embed=true`

	const config = {
		url: queryString,
		method: 'get'
	}
	return Request(config);
}
 

export const API = {
	BASE_URL,
	getPostList,
	getCategory,
	getCategoryList,
	getPostsByFormat,
	getPostQuery
};