import React, { Component, Fragment } from 'react';
import { API, HELPER } from '../utils'
import ContentLoader from "react-content-loader";
import { Card } from "../molecules"
import { Header, Navigation, Cards, FeedList } from '../organisms'
import { Modal } from '../molecules'
import { Badges } from '../atom'


class Home extends Component {

	state = {
		posts: [],
		modalConfig: {
			title: 'Susanto',
			links: {
				follow: 'follow_link',
				category: 'category_link',
				profile: 'profile_user_link',
				detail: 'detail_post_link'
			},
			isOpen: false,
		}
	}

	_isMounted = false;

	componentDidMount() {
		this._isMounted = true;
		// console.log('Hello World!');
		API.getPostList(3,6).then(res => {
			// console.log('REQUEST: ', res)
			if(this._isMounted){
				this.setState({
					posts: res.data
				})
			}
			
		})
	}

	handleModal = (data: any, openModal: string) => {
		// console.log('Passing Dato To Modal: ', data)
		const modalConfig = { ...this.state.modalConfig }
		if (openModal === 'open') {
			// Set Modal Config What We Want
			modalConfig.title = 'Thomi Jasir'
			modalConfig.isOpen = !modalConfig.isOpen
		} else {
			modalConfig.isOpen = !modalConfig.isOpen
		}
		this.setState({
			modalConfig
		})
	}

	componentWillUnmount() {
		this._isMounted = false;
	}	

	render() {
		const { modalConfig, posts } = this.state

		return (
			<Fragment>
				<div className="container">
					<Header />
					<Navigation />
					{/* Section Trending */}
					<br></br>
					<section>
					
					</section>
					<section>
						<Cards handleModal={this.handleModal} data={{
							cardType: 'video',
							endpoint: 0,
						}}/>
					</section>
					
					{/* Section Video List */}
					<section>
					<Cards handleModal={this.handleModal} data={{
							cardType: 'standard',
							endpoint: 1,
						}}/>
					</section>

					{/* Section News Feed */}
					<section>
						<div className="row row-no-padding match-heigh">
							{posts.map((item: any, index: number) => (
								<div className="col-sm-12 col-md-6 col-lg-4" key={index}>
									<div className={'content-box gradient-' + HELPER.getRandomInt(0, 10)}>
										<div className="box-nav">
											<div className="box-label">
											<Badges idCategories={item.categories} endpoint={3}/>
											</div>
											<div className="button-menu" onClick={() => this.handleModal(item.id, 'open')}>
												<div className="ripple">
													<i className="fas fa-ellipsis-v"></i>
												</div>
											</div>
										</div>

										<Card isSmall={false} data={{
											title: HELPER.parsePostTitle(item.title.rendered),
											description: HELPER.parseExcerpt(item.excerpt.rendered),
											image: item._embedded['wp:featuredmedia'][0].source_url,
											dateTime: HELPER.timeAgo(item.date),
											author: item._embedded.author[0].name,
											authorImage: item._embedded.author[0].avatar_urls["48"],
											authorSlug: item._embedded.author[0].slug,
											slug: item.slug
										}}/>

									</div> { /* END CONTENT BOX */}
								</div>
							))}
						</div>
					</section>
					
					<section>
						<div className="row match-heigh feed-section">
							<div className="col-sm-12 col-lg-4 col-lg-push-8">
								<div className="recommendation position-sticky">
									<div className="head">
										<h3>Rekomendasi Bicara</h3>
									</div>
									<div className="list">
									
										<div className="item">
											<div className="item-number">
												01
											</div>
											<div className="item-content">
												<h4>
													<a href="https://www.youtube.com/watch?v=_yuDDapUHKk" className="default-link">
														Apa itu Nebula? Darimana Asalnya Bagaimana Terbe..
													</a>
												</h4>
												<a>Bicara Indonesia</a>
												<span>Feb 25, 2019</span>
											</div>
										</div>
										<div className="item">
											<div className="item-number">
												02
											</div>
											<div className="item-content">
												<h4>
													<a href="https://www.youtube.com/watch?v=4IqGKEqkaD4" className="default-link">
														Mengapa Planet Di Tata Surya Mengelilingi Matahari?
													</a>
												</h4>
												<a>Bicara Indonesia</a>
												<span>Feb 17, 2019</span>
											</div>
										</div>
										<div className="item">
											<div className="item-number">
												03
											</div>
											<div className="item-content">
												<h4>
													<a href="https://www.youtube.com/watch?v=2-6VkxyGdPE" className="default-link">
														Jika Bumi Keluar Orbit Kemana Bumi Akan Pergi?
													</a>
												</h4>
												<a>Bicara Indonesia</a>
												<span>Feb 16, 2019</span>
											</div>
										</div>

									</div>
								</div>
							</div>
							<div className="col-sm-12 col-lg-8 col-lg-pull-4">
								<FeedList/>
							</div>
						</div>
					</section>
					
				</div>
				{/* EMBEDDING COMPONENT */}
				<Modal config={modalConfig} closeModal={this.handleModal} />
			</Fragment>
		);
	}
}

export default Home;
