import React, { PureComponent } from "react";
import { API, HELPER } from '../utils';

import ContentLoader from "react-content-loader";
import Badge from './Badge';

interface IBadges {
	idCategories: Array<number>,
	endpoint: number
}

class Badges extends PureComponent<IBadges> {
	_isMounted = false;

	static defaultProps = {
		endpoint: 0
	}

	constructor(props: any) {
		super(props)
		this.state = {
			isLoading: false,
			badges: []
		}
	}

	setLoading = (status: boolean): void => {
		this.setState({
			isLoading: status
		})
	}

	init = async () => {
		this.setLoading(true)
		const listCat = this.props.idCategories
		const endpoint = this.props.endpoint
		const tempBadge = [];
		for(let i = 0; i < 2; i++){
			if(listCat[i]) {
				const temp = await API.getCategory(endpoint, listCat[i])
				tempBadge.push(temp.data)
			} else {
				break;
			}
		}
		if(this._isMounted){
			this.setState({
				isLoading: false,
				badges: tempBadge
			})
		}
	} 

	componentDidMount(){
		this._isMounted = true;
		this.init()
	}

	componentWillUnmount() {
    this._isMounted = false;
  }
	render() {
		const { badges, isLoading }: any = this.state
		if(isLoading) {
			return '...'
		}
		return (
			<div className="badges">
				{
					badges.map((item: any, index: number) => {
						const config = {
							key: `${item.id}-${index}`,
							name: item.name,
							link: `/topic/${item.slug}`,
						}
						return <Badge {...config}/>
					}
					)
				}
			</div>
		);
	}
}

const LabelLoader = () => {
	return (
		<ContentLoader 
			height={60}
			style={{
				width: '100%',
				padding:'15px' 
			}}
			preserveAspectRatio="none"
			speed={1}
			primaryColor="#f3f3f3"
			secondaryColor="#ecebeb"
			>
			<rect x="0" y="0" rx="15" ry="15" width="100" height="35" /> 
		</ContentLoader>
	);
};


export default Badges;