import * as React from 'react'
import { Link } from 'react-router-dom'

interface IBadge {
 name?: string,
 icon?: string,
 link: string,
}
const Badge: React.SFC<IBadge> = (props) => {
 return (
   <Link to={props.link}>
    <div className="badge auto ripple">
      <i className="fas fa-award"></i>
      <label>{props.name}</label>
    </div>
  </Link>
 )
}
Badge.defaultProps = {
 name: 'Uncategorize',
 icon: 'fa-award',
 link: '/'
}

export default Badge;