import React from 'react'
import classNames from 'classnames'

const Modal = (props: any) => {
    // console.log('Props Render: ', props)
    const { config, closeModal } = props
    const ehtml = document.querySelector('html')

    // Disable Scrolling When Open Modals
    if(ehtml != null){
        if(config.isOpen) {
            ehtml.classList.add("no-scroll");
        } else {
            ehtml.classList.remove("no-scroll");
        }
    }
        return(
            <div className={classNames('modals',{'show':config.isOpen})}>
                <div className="modal-wrapper">
                    <div className="modal-content">
                        <div className="head">
                            <div className="head-left">
                                <h3>{ config.title }</h3>
                            </div>
                            <div className="head-right">
                                <button className="btn primary rouded bordered shadow ripple">
                                    <i className="fas fa-plus mg-r-5"></i> Follow
                                </button>
                            </div>
                        </div>
                        <div className="body">
                            <ul className="list-item">
                                <li className="ripple"><i className="fa fa-link mg-r-10"></i> Lihat Cerita Terkait</li>
                                <li className="ripple"><i className="fa fa-user mg-r-10"></i> Lihat User Profile</li>
                                <li className="ripple"><i className="fa fa-ellipsis-h mg-r-10"></i> Lihat Lebih Detail</li>
                            </ul>
                        </div>
                        <div className="footer ripple" onClick={() => closeModal(null, 'close')}>
                            <p><i className="far fa-times-circle mg-r-5"></i> Close</p>
                        </div>
                    </div>
                </div>
                {/* <div className="modal-layer"></div> */}
            </div>
        )
}

export default  Modal;
